import Vue from 'vue';
import App from './App.vue';
import router from './router/router';
import VueHead from 'vue-head';
import i18n from './i18n';

Vue.config.productionTip = false;
Vue.use(VueHead);

new Vue({
  router,
  render: (h) => h(App),
  i18n,

  head: {
    meta: [
      {name: 'viewport', content: 'width=device-width'}
    ]
  }
}).$mount('#app');
