

























import { Component, Prop, Vue } from 'vue-property-decorator';
import SlideInBlurOut from '@/components/Animations/SlideInBlurOut.vue';
import BlurOut from '@/components/Animations/BlurOut.vue';
import HappySmileyIcon from '@/components/icons/HappySmileyIcon.vue';
import ArrowRight from 'mdi-vue/ArrowRight.vue';

@Component({
  components: {
    SlideInBlurOut,
    BlurOut,
    ArrowRight,
    HappySmileyIcon
  }
})
export default class LandingPageInfo extends Vue {
  counter = 0;
  textToWrite = ''; /* Text to write by the typewriter */
  writingComplete = false;
  showHello = true;

  async mounted() {
    await this.writeHeader();
  }

  async writeHeader() {
    setTimeout(async () => {
      this.showHello = false;
      await this.wait(1100);
      this.textToWrite = this.$t('iam') as string;
      await this.startTypeWriter('i-am', 50);
      this.counter = 0;
      this.textToWrite = ' Marius Bongarts';
      await this.startTypeWriter('my-name', 50);
      await this.wait(500);
      this.counter = 0;
      this.textToWrite = this.$t('headerSubLine') as string;
      await this.startTypeWriter('my-description', 30);
      await this.wait(200);
      this.writingComplete = true;
    }, 1000);
  }

  async wait(millis: number) {
    return new Promise(res => setTimeout(() => res(), millis));
  }

  private navigate(route: string) {
    this.$router.push(route);
  }

  private scrollToSection() {
    const sectionElement = document.getElementById('section-home');
    if (sectionElement) {
      sectionElement.scrollIntoView({
        behavior: 'smooth',
        block: 'end',
        inline: 'nearest'
      });
    }
  }

  private async startTypeWriter(elementId: string, speed: number) {
    return new Promise(resolve => {
      if (this.counter === this.textToWrite.length) resolve();
      if (this.counter < this.textToWrite.length) {
        const element = document.getElementById(elementId) as HTMLElement;
        element.innerHTML += this.textToWrite.charAt(this.counter);
        this.counter = this.counter + 1;
        setTimeout(async () => {
          await this.startTypeWriter(elementId, speed);
          resolve();
        }, speed);
      }
    });
  }
}
