




















import { Component, Vue, Prop } from 'vue-property-decorator';
import LandingPage from '@/components/LandingPage.vue';
import NavBar from '@/components/NavBar.vue';
import { NavItem } from '@/models/navItem';
import SlideIn from '@/components/Animations/SlideIn.vue';

@Component({
  components: {
    LandingPage,
    NavBar,
    SlideIn
  }
})
export default class HomePage extends Vue {
  navItems: NavItem[] = [];

  mounted() {
    this.navItems = [
      {
        displayName: this.$t('navigation.home') as string,
        sectionName: 'section-home',
        active: true
      },
      {
        displayName: this.$t('navigation.about') as string,
        sectionName: 'section-about',
        active: false
      },
      {
        displayName: this.$t('navigation.portfolio') as string,
        sectionName: 'section-portfolio',
        active: false
      },
      {
        displayName: this.$t('navigation.contact') as string,
        sectionName: 'section-contact',
        active: false
      }
    ];
  }

  selectNavItem(navItem: NavItem) {
    this.navItems = this.navItems.map(item =>
      item === navItem ? { ...item, active: true } : { ...item, active: false }
    );
  }
}
